  methods: {
    ...mapMutations([
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType'
    ]),
    back () {
      this.$router.go(-1)
    },
    // 设置选中状态
    setSelect (index, optionIndex) {
      let temp = this.askList[index]
      let tempOption = this.askList[index].options[optionIndex]
      if (temp.type === 'single') {
        for (let [i, v] of this.askList[index].options.entries()) {
          if (tempOption.ansWerId === v.ansWerId) {
            this.$set(this.askList[index].options[i], 'selected', true)
          } else {
            this.$set(this.askList[index].options[i], 'selected', false)
          }
        }
      } else if (temp.type === 'multi') {
        let count = 0
        for (let i of this.askList[index].options.keys()) {
          if (this.askList[index].options[i].selected) {
            count++
          }
        }
        for (let [i, v] of this.askList[index].options.entries()) {
          if (tempOption.ansWerId === v.ansWerId) {
            if (tempOption.selected) {
              this.$set(this.askList[index].options[i], 'selected', false)
            } else {
              if (count < 5) {
                this.$set(this.askList[index].options[i], 'selected', true)
              } else {
                this.$toast({
                  message: '最多选择5个'
                })
              }
            }
          }
        }
      }
    },
    // 完成提交
    complete () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      let arr = []
      for (let [i, v] of this.askList.entries()) {
        let answer = {
          askId: v.askId,
          type: v.type
        }
        let answers = []
        for (let option of v.options.values()) {
          if (option.selected) {
            answers.push(option.ansWerId)
            if (v.type === 'single') {
              break
            }
          }
        }
        if (answers.length === 0) {
          return this.$toast({
            message: `第${i + 1}道题未作答，请补充作答`,
            duration: 1500
          })
        }
        answer.answerIds = answers.join(',')
        arr.push(answer)
      }
      let data = {
        answer: JSON.stringify(arr),
        remark: this.content.trim()
      }
      this.$api.post('QUESTION_SAVE', data, res => {
        if (!res.data.err_code) {
          let shareSuccessObj = Object.assign({type: 9}, res.data)
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj
          })
          this.set_shareType(7)
          this.setShowShare(true)
          this.back()
        }
      })
    },
    // 获取题目数据
    getAskList () {
      this.$api.get('GET_ASKLIST', {}, res => {
        this.askList = res.data
      })
    }
  },
  created () {
    this.getAskList()
  }
