      line-height: 42px;
      margin: 50px 0 100px;
    }
    .complete{
      height: 100px;
      background: #DDDDDD;
      border-radius: 18px;
      color: #fff;
      margin-bottom: 100px;
    }
    .complete.active{
      background: #F5A623;
    }
    .answerItem{
      padding-top: 80px;
      .answerTitle{
        font-size: 36px;
        color: #111111;
        line-height: 43px;
        text-align: left;
      }
      .answerSecList{
        flex-flow: row wrap;
        padding-top: 30px;
        .answerOption {
          width: 100%;
          background: #FFFFFF;
          border-radius: 18px;
          margin-bottom: 20px;
          height: 98px;
          padding: 0 20px;
          box-sizing: border-box;
          .leftIcon {
            width: 31px;
            height: 31px;
            margin-right: 10px;
          }
        }
        .answerOption.selected {
          background: #F5A623;
          color: #fff;
        }
        .answerInput{
          textarea{
            width: 100%;
            background: #FFFFFF;
            border-radius: 18px;
            height: 200px;
            font-family: PingFangSC-Regular;
            font-size: 30px;
            color: #232323;
            line-height: 61px;
            padding: 10px;
            box-sizing: border-box;
          }
        }
      }
      .answerSecListmulti{
        justify-content: space-between;
        .answerOption{
          padding-right: 0;
          width: calc(50% - 15px);
        }
      }
